.link-animation {
  position: relative;
  color: white;
  text-decoration: none;
}

.link-animation::after {
  content: "";
  position: absolute;
  width: 0;
  left: 0;
  height: 2px;
  background: #DEE2E5;
  -webkit-transition: all 0.3s ease all 0s;
  transition: all 0.3s ease all 0s;
}

.link-animation:hover::after {
  width: 100%;
  bottom: -10%;
  -webkit-transition: 0.3s ease all;
  transition: 0.3s ease all;
}

