.about-background {
  background-color: #DEE2E5;
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0%;
  left: 0%;
  overflow: auto;
  display: block;
}

.profile-box {
  float: right;
  width: 25vw;
  background-color: white;
}

.boxed {
  width: 90vw;
  height: 80vh;
  background-color: white;
  border-radius: .5%;
  border: 2px solid grey;
  padding: 2.5%;
  box-shadow: 15px -15px 0px 0px grey;
  left: 0%;
  margin: 2.5%;
}

.about-shadow {
  position: relative;
  width: 15vw;
  height: 5px;
  background-color: #DEE2E5;
}

.project-background {
  background-color: #ACC8E5;
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0%;
  left: 0%;
  overflow: auto;
  display: block;
}

.project-box {
  display: absolute;
  border-radius: .5%;
  font-size: 100%;
  width: 90vw;
  height: 85vh;
  background-color: white;
  padding: 2.5%;
  padding-top: 1%;
  left: 0%;
  margin: 2.5%;
  margin-bottom: 0%;
  overflow-y: scroll;
}

.project-shadow {
  position: relative;
  width: 15vw;
  height: 5px;
  background-color: #ACC8E5;
}

.project-titles {
  font-size: 150%;
}

.project-text {
  background-color: white;
  border-radius: .5%;
  width: 85vw;
  height: 65vh;
  border: 1px solid #84A8CC;
  padding: 2.5%;
  margin-top: 2.5%;
}

.project-boxes {
  position: relative;
  display: inline-block;
}

.image {
  display: block;
  width: 100%;
  height: auto;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  -webkit-transition: .5s ease;
  transition: .5s ease;
}

.overlay:hover {
  -webkit-transition: .5s ease;
  transition: .5s ease;
  opacity: .75;
}

.proj-text {
  font-size: 15px;
  padding: 10px;
  background-color: white;
  width: 200px;
  height: 200px;
  padding: 10px;
  margin: 10px;
  border: 1px 1px solid #ACC8E5;
  color: black;
}

.proj-box {
  font-size: 20px;
  width: 200px;
  height: 200px;
  padding: 10px;
  margin: 10px;
  background-color: #ACC8E5;
  text-align: center;
}

a {
  color: white;
  text-decoration: none;
}

a:hover {
  color: #ACC8E5;
}

.coffee-background {
  background-color: white;
  position: relative;
  width: 90vw;
  height: 100vh;
  margin: 0%;
  margin-left: 2.5%;
  left: 0%;
  overflow: auto;
  display: block;
}

.coffee-shadow {
  position: relative;
  width: 15vw;
  height: 5px;
  background-color: #FCE3C9;
}

.coffee-pictures {
  float: right;
  width: 25vw;
  height: 75vh;
  background-color: white;
  margin-top: 0%;
  margin-right: 5%;
}

.text {
  background-color: white;

}

.coffee-link-animation {
  position: relative;
  color: #FCE3C9;
  text-decoration: none;
}

.coffee-link-animation:hover {
  color: #FCD1A4;
}

.coffee-link-animation::after {
  content: "";
  position: absolute;
  width: 0;
  left: 0;
  height: 1px;
  background: #FCD1A4;
  -webkit-transition: all 0.3s ease all 0s;
  transition: all 0.3s ease all 0s;
}

.coffee-link-animation:hover::after {
  width: 100%;
  bottom: -10%;
  -webkit-transition: 0.3s ease all;
  transition: 0.3s ease all;
}

form {
  /* Just to center the form on the page */
  position: relative;
  left: 10px;
  width: 25vw;
  /* To see the outline of the form */
  padding-top: 0;
  padding: 15px;
  border: 1px solid #FCD1A4;
  border-radius: 1%;
}

label {
  /* To make sure that all labels have the same size and are properly aligned */
  display: inline-block;
  width: 90px;
  text-align: right;
}

input, textarea {
  font-family: 'Cutive Mono', monospace;
  width: 25vw;
  box-sizing: content-box;

  /* To harmonize the look & feel of text field border */
  border: 1px solid #999;
}

input:focus, textarea:focus {
  /* To give a little highlight on active elements */
  border-color: #000;
}

textarea {
  /* To properly align multiline text fields with their labels */
  vertical-align: top;

  /* To give enough room to type some text */
  height: 5em;
}

.button {
  /* To position the buttons to the same position of the text fields */
  padding-top: 2.5%; /* same size as the label elements */
}

button {
  font-family: 'Cutive Mono', monospace;
  font-size: 85%;
  margin-left: 0%;
}

.contact-background {
  background-color: #FCE3C9;
  position: relative;
  width: 100vw;
  height: 12.5vh;
  margin: 0%;
  left: 0%;
  overflow: auto;
  display: block;
}

.contact-text {
  align: center;
  background-color: #FCE3C9;
  font-size: 125%;
  position: relative;
  width: 65vw;
  height: 10vh;
  margin-top: 2.5vh;
  margin-right: 20px;
  display: block;
  margin-left: auto;
}

img {
  align: right;
}

