.project-boxes {
  position: relative;
  display: inline-block;
}

.image {
  display: block;
  width: 100%;
  height: auto;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  transition: .5s ease;
}

.overlay:hover {
  transition: .5s ease;
  opacity: .75;
}

.proj-text {
  font-size: 15px;
  padding: 10px;
  background-color: white;
  width: 200px;
  height: 200px;
  padding: 10px;
  margin: 10px;
  border: 1px 1px solid #ACC8E5;
  color: black;
}

.proj-box {
  font-size: 20px;
  width: 200px;
  height: 200px;
  padding: 10px;
  margin: 10px;
  background-color: #ACC8E5;
  text-align: center;
}

a {
  color: white;
  text-decoration: none;
}

a:hover {
  color: #ACC8E5;
}
